.modal {
  display: none; }
  .modal.modal-open {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 3; }
  @media (min-width: 1024px) {
    .modal.modal-on-mobile-only {
      background-color: transparent;
      height: auto; }
      .modal.modal-on-mobile-only .modal-inner {
        position: initial;
        padding: 0;
        width: auto;
        height: auto;
        top: inherit;
        left: inherit; }
      .modal.modal-on-mobile-only .modal-close {
        display: none; } }

.modal-inner {
  position: relative;
  z-index: 4;
  background-color: #fff;
  width: 90%;
  height: 90%;
  top: 5%;
  left: 5%;
  overflow: auto;
  padding: 1.25rem; }
  @media (min-width: 768px) {
    .modal-inner {
      padding-top: 3.75rem; } }

.modal-close {
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  padding: 0.25rem;
  background-color: #cfcfcf;
  text-align: center;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  cursor: pointer;
  outline: none; }
  .modal-close:hover {
    background-color: #c1c1c1; }



