.resources-wrap h2 {
  text-transform: uppercase;
  font-family: "bebas_neuebold", sans-serif;
  font-size: 2.25rem; }

.wordclouds-wrap {
  margin-bottom: 1.5rem; }
  @media (min-width: 480px) {
    .wordclouds-wrap {
      display: grid;
      grid-template-columns: repeat(2, 1fr); } }

@media (min-width: 768px) {
  .resources-content-slider .slick-arrow {
    top: 0.5rem; } }

.resources-content-slider .slide-inner {
  text-align: center;
  outline: none; }
  .resources-content-slider .slide-inner:focus, .resources-content-slider .slide-inner:active {
    outline: none; }

.resources-content-slider .slide-header {
  display: flex;
  justify-content: space-between; }
  @media (min-width: 768px) {
    .resources-content-slider .slide-header {
      margin-bottom: 1.5rem; } }
  .resources-content-slider .slide-header h3 {
    margin: 0 2.5rem; }

.resources-content-slider .slide-content {
  max-width: 64rem;
  margin: 0 auto;
  padding: 0 2rem; }

.resources-content-slider .slide-arrow {
  cursor: pointer;
  margin-top: 2rem;
  outline: none; }
  @media (min-width: 768px) {
    .resources-content-slider .slide-arrow {
      margin-top: 0; } }
  .resources-content-slider .slide-arrow span {
    display: none; }
    @media (min-width: 768px) {
      .resources-content-slider .slide-arrow span {
        display: inline-block;
        margin: 0 0.5rem;
        font-size: 0.75rem; } }



