/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'bebas_neuebold';
  src: url("/static/fonts/bebasneue_bold-webfont.eot");
  src: url("/static/fonts/bebasneue_bold-webfont.eot?#iefix") format("embedded-opentype"), url("/static/fonts/bebasneue_bold-webfont.woff2") format("woff2"), url("/static/fonts/bebasneue_bold-webfont.woff") format("woff"), url("/static/fonts/bebasneue_bold-webfont.ttf") format("truetype"), url("/static/fonts/bebasneue_bold-webfont.svg#bebas_neuebold") format("svg");
  font-weight: normal;
  font-style: normal; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: "Roboto Mono", monospace;
  font-size: 1rem;
  line-height: 1.5rem;
  color: #666; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700; }

strong {
  font-weight: 700; }

input,
button {
  font-family: "Roboto Mono", monospace; }

.page-wrapper {
  min-width: 20rem; }

.container {
  max-width: 37.5rem;
  margin: 0 auto;
  padding: 0 1.5rem; }



