.wordcloud {
  text-align: center;
  padding: 1.5rem;
  cursor: pointer;
  outline: none; }
  .wordcloud h3 {
    margin-bottom: 0.5rem; }
  .wordcloud span {
    display: inline-block;
    padding: 0 0.5rem; }
    .wordcloud span.size-1 {
      font-size: 1.875rem; }
    .wordcloud span.size-2 {
      font-size: 1.5625rem; }
    .wordcloud span.size-3 {
      font-size: 1.25rem; }
    .wordcloud span.size-4 {
      font-size: 0.9375rem; }



